.page {
  width: 320px;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  background-color: #fff;
  position: fixed;
  transform: translateX(-860px);
  transition: transform .2s;
  z-index: 2; }

.page.showMenu {
  transform: translateX(0px); }

.page__navigate {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }

/*-------------------------fx-----*/
.navFX_hoverPink:hover {
  border-radius: 2px;
  background-color: #ff3e79;
  cursor: pointer; }

@-webkit-keyframes borderglide {
  0% {
    background-image: linear-gradient(to right, white, white);
    opacity: 0.5; }
  100% {
    background-image: linear-gradient(to right, white, #cfcfcf 100%);
    opacity: 1; } }

@keyframes borderglide {
  0% {
    background-image: linear-gradient(to right, white, white);
    opacity: 0.5; }
  100% {
    background-image: linear-gradient(to right, white, #cfcfcf 100%);
    opacity: 1; } }

.glideFX_hover:hover {
  -webkit-animation-name: borderglide;
          animation-name: borderglide;
  -webkit-animation-duration: 0s;
          animation-duration: 0s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  cursor: pointer; }

